<template>
    <div class="register">
        <van-nav-bar
            title="注册"
            left-text="登录"
            left-arrow
            right-text="关闭"
            @click-left="onClickLeft"
            @click-right="shut"
        />

        <div class="logo">
            <img src="../assets/logo.png" alt="">
        </div>

        <van-form @submit="register">
            <van-field
                v-model="userInfo.phone"
                type="text"
                name="手机号"
                label="手机号"
                placeholder="手机号"
            >
                <template v-slot:left-icon>
                    <i class="fa fa-phone fa-lg" aria-hidden="true"></i>
                </template>
            </van-field>

            <van-field
                v-model="userInfo.nickName"
                type="text"
                name="昵称"
                label="昵称"
                placeholder="昵称"
            >
                <template v-slot:left-icon>
                    <i class="fa fa-user-circle" aria-hidden="true"></i>
                </template>
            </van-field>

            <van-field
                v-model="userInfo.password"
                :type="flag1 ? 'password' : 'text'"
                name="密码"
                label="密码"
                placeholder="密码"
                @click-right-icon="flag1 = !flag1"
            >
                <template v-slot:left-icon>
                    <i class="fa fa-unlock-alt fa-lg" aria-hidden="true"></i>
                </template>
                <template v-slot:right-icon>
                    <i class="fa fa-lg" aria-hidden="true" :class="[flag1 ? 'fa-eye-slash' : 'fa-eye']"></i>
                </template>
            </van-field>

            <van-field
                v-model="password2"
                :type="flag2 ? 'password' : 'text'"
                name="确认密码"
                label="确认密码"
                placeholder="确认密码"
                @click-right-icon="flag2 = !flag2"
            >
                <template v-slot:left-icon>
                    <i class="fa fa-unlock-alt fa-lg" aria-hidden="true"></i>
                </template>
                <template v-slot:right-icon>
                    <i class="fa fa-lg" aria-hidden="true" :class="[flag2 ? 'fa-eye-slash' : 'fa-eye']"></i>
                </template>
            </van-field>

            <div class="btn">
                <van-button round block type="info" native-type="submit">
                注册
                </van-button>
            </div>
        </van-form>
    </div>
</template>

<script>

    import { Toast } from 'vant';

    // 导入验证变淡模块
    import validForm from "../assets/js/validForm";

    export default {
        name: 'Register',
        data() {
            return {
                flag1: true,
                flag2: true, 
                password2: null,
                
                //用户注册信息
                userInfo: {
                    phone: null,
                    nickName: null,
                    password: null,
                },
 
            };
        },
        methods: {
            // 注册
            register() {

                if(this.userInfo.password != this.password2) {

                    Toast("输入两次密码不一致");

                }else{

                    if(validForm.validateForm(this.userInfo)) {
                        let data = Object.assign({}, this.userInfo);

                        // 请求密钥
                        data.appkey = this.appkey;

                        // 开启加载提示
                        Toast.loading({

                            message: "加载中...",
                            forbidClick: true,
                            //延迟自动关闭, 如果该值为0, 则不会自动关闭
                            duration: 0,

                        });

                        this.axios({
                            method: "POST",
                            url: "/register",

                            // post传递参数需要写在data
                            data,
                        }).then(res => {
                            // 关闭加载提示
                            Toast.clear();


                            if(res.data.code == 100){

                                Toast.success(res.data.msg);

                                // 跳转到登录页面
                                this.$router.push({name: "Login"});

                            } else{

                                Toast.fail(res.data.msg);

                            }
                            
                        }).catch(err => {
                            // 关闭加载提示
                            Toast.clear();

                            
                        });

                    }

                }
                
            },

            // 跳转登录页面
            onClickLeft(){
                this.$router.push({name: "Login"});
            },

            // 关闭
            shut(){
                this.$router.push({ name: "Home" });
            }
        },
    }
</script>

<style lang="less" scoped>
    // /deep/ .van-nav-bar__text {
