    // }
    .logo {
        width: 100px;
        height: 100px;
        margin: 50px auto;

        img {
            width: 100%;
            height: 100%;
        }
    }

    .van-field {
        color: #666;
    }

    .btn {
        margin: 30px 20px;
    }
</style>
